header{
	background: $green;
	height: $headerTopHeight;
	left: 0;
	min-width: $min-width;
	order: 1;
	padding: 0 2rem;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 1;

	.wrapper {
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 100%;
	}

}//header

